* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    overflow: hidden;
}

.background { 
    background-color: black;
    width: 100vw;
    height: 100vh;
    background-image: url('./galaxy-bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.mainMenu {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        border: solid white 2px;
        border-radius: 1rem;
        width: 50%;
        background-color: rgba(128, 128, 128, 0.308);
        p {
            font-size: 1.6rem;
            margin-bottom: 1rem;
        }
        button {
            padding: 10px 20px;
            background-color: rgba(0, 0, 0, 0.493);
            border: none;
            border-radius: 1rem;
            color: white;
            cursor: pointer;
            font-size: 1.2rem;
            &:hover {
                background-color: rgba(255, 255, 255, 0.685);
                color: black;
            }
        }
    }
}

.gamescreen {
    position: relative;
    width: 100vw;
    height: 100vh;
}

.earth {
    height: auto;
    width: 220px;
    position: absolute;
    left: 50%;
    margin-left: -50px;
    top: 30%;
    animation: floating 6s infinite;
}

.ufo {
    &__1 {
        height: auto;
        width: 200px;
        animation: floating 1s infinite;
        position: absolute;
        top: 20%;
        left: 40%;
    }
    &__2 {
        height: auto;
        width: 200px;
        animation: floating 2s infinite;
        position: absolute;
        top: 30%;
        left: 52%;
    }
    &__3 {
        height: auto;
        width: 200px;
        animation: floating 4s infinite;
        position: absolute;
        top: 40%;
        left: 38%;
    }
    &__4 {
        height: auto;
        width: 200px;
        animation: floating 2s infinite;
        position: absolute;
        top: 20%;
        left: 60%;
    }
    &__5 {
        height: auto;
        width: 200px;
        animation: floating 3s infinite;
        position: absolute;
        top: 48%;
        left: 60%;
    }
}

@keyframes floating {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(0);
    }
}

.health-position {
    display: flex;
    color: white;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    p {
        font-size: 1.6rem;
    }

    .health {
        width: 500px;
        height: 20px;
        background-color: red;
        border-radius: 20px;
        &__inside {
            background-color: green;
            height: 100%;
            border-radius: 20px;
            border: solid white 2px;
        }
    }
}

.question-placement {
    color: white;
    position: absolute;
    bottom: 20%;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.question {
    background-color: rgba(128, 128, 128, 0.308);
    border: solid white 2px;
    border-radius: 10px;
    width: 800px;
    display: flex;
    padding: 1rem;
    p {
        font-size: 1.6rem;
        margin-right: 10px;
    }
}

.answer-position {
    width: 800px;
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    p {
        font-size: 1.1rem;
        padding: 1rem;
        background-color: rgba(128, 128, 128, 0.308);
        border: solid white 2px;
        border-radius: 10px;
        width: 230px;
        cursor: pointer;
        &:hover {
            background-color: white;
            color: black;
        }
    }
}

.win {
    background-color: rgba(128, 128, 128, 0.308);
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: solid white 2px;
    border-radius: 10px;
    width: 500px;
    color: white;
    position: absolute;
    left: 50%;
    margin-left: -250px;
    bottom:40%;
    h1 {
        margin-bottom: 20px;
        text-align: center;
    }
    button {
        font-size: 1.4rem;
        padding: 1rem;
        background-color: rgba(128, 128, 128, 0.308);
        border: solid white 2px;
        border-radius: 10px;
        color: white;
        cursor: pointer;
        &:hover {
            background-color: white;
            color: black;
        }
    }
}

.tip {
    background-color: rgba(128, 128, 128, 0.308);
    padding: 20px;
    border: solid white 2px;
    border-radius: 10px;
    color: white;
    position: absolute;
    top: -100%;
    font-size: 1.2rem;
}